@import '../globals';

.productContainer {
    @include center-aligned;
    margin-bottom: 30px;
}

.productImg {
    width: 20%;
    cursor: pointer;
}

.productTitle {
    font-size: larger;
    cursor: pointer;
}

.arrowIcon {
    width: 7%;
    margin-right: 20px;
    cursor: pointer;
}

.rightArrowIcon {
    @extend .arrowIcon;
    background-color: $cyan;
    border-radius: 30px;
}

.leftArrowIcon {
    @extend .arrowIcon;
    transform: rotate(180deg);
    filter: invert(31%) sepia(99%) saturate(3833%) hue-rotate(186deg) brightness(85%) contrast(89%);
}

@media only screen and (max-width: 768px) {
    .productTitle {
        font-size: small;
    }
    .arrowIcon {
        width: 10%;
    }
}